:root {
  --primary-white: #fff;
  --primary-orange: #C89832;
  --primary-dark: rgba(255,255,255,0);
  --primary-red: #ef4035;
  --primary-hover-red: #f33501;
}

body{
	font-family: 'Noto Sans TC', sans-serif;
}

.navbar {
	color: rgba(255,255,255,0) !important;
	color: var(--primary-dark) !important;
}

.nav-link {
	color: #fff !important;
	color: var(--primary-white) !important;
}

.navbar-brand img{
	height: 40px !important;
	width: 100% !important;
}

.nav-item {
	transition: ease-in 0.5s;
}

.nav-item:hover{
	border: 2px solid #fff;
	border: 2px solid var(--primary-white);
	background-color: #C89832;
	background-color: var(--primary-orange);
	border-radius: 5px;
}


.nav-link:hover {
	color: #fff !important;
	color: var(--primary-white) !important;
	/* border: 1px solid var(--primary-orange); */
} 






.header-tien {
    position: relative;
    background: url(/Templewebsite/static/media/天后2.e94ca473.jpg) no-repeat;
    background-size: cover;
    background-position: center;
    height: 90vh;
    align-items: center;
    text-align: center;
    margin-bottom: 15px;
  }


.main-info-tien {
    display: flex;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
  }

.main-info-tien h1 {
    font-size: 60px;
    color: white;
    letter-spacing: 10px;
    background: rgba(0, 0, 0, 0.5);
    box-shadow: 0 0 0 10px rgba(0, 0, 0, 0.5);
  }

.description {
    margin-top: 30px;
    padding: 15px;
    align-items: center;
    border-bottom: #C89832 solid;
}

.word {
    color: black;
    letter-spacing: 3px;
}

.word h2 {
  color: rgb(194, 21, 47);  
  margin-bottom: 30px;
}

.info-box {
  margin-top: 30px;
  border-bottom: #C89832 solid;
}

.info-box iframe {
  height: 500px;
  width: 700px;
  border: 0;
}

.info-box .info-word {
  color: rgb(194, 21, 47);
  border-top: #4a4a4a 1px solid;
  padding: 30px 0;
  letter-spacing: 2px;
}

.info-box p {
  color: #000000;
  font-size: 20px;
  font-weight: lighter;
}

.collection {
  margin-top: 30px;
  color: rgb(194, 21, 47);
}

.slider {
  display: block;
}

.slick-prev:before,
.slick-next:before {
  color: #000000 !important;
}

.slider img {
  max-width: 300px;
  max-height: 300px;
}

.slider img:hover {
  cursor: pointer;
}

.dialog {
  display: flex;
  align-content: center;
  justify-items: center;
  justify-content: center;
}
.dialog img{
  display: flex;
  max-width: 500px;
  max-height: 300px;
}

.dialog p{
  display: flex;
  max-width: 500px;
}

